body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container > h1{
  text-align: center;
  padding: 20px;
  margin: 10px;
  background-color: aqua;
}
.content{
  display: flex;
  gap: 20px;
  margin: 10px;
  padding: 10px;
  justify-content: space-evenly;
  
}
.content > .weather-detail{
   color: rgb(182, 34, 125);
  padding: 10px;
  outline: 3px solid greenyellow;
  
}

.weather-img{
  width: 100%px;
  height: 100%;
  text-align: center;
  outline: 3px solid greenyellow;
}
.weather-img > img{
  width: 400px;
  z-index: 5;
}
.container > button{
  padding: 10px;
  background-color: #4CAF50;
  border: none;
  border-radius: 5px;
  color: black;
  font-size: 20px;
  position: absolute;
  left: 600px;
  bottom: 50px;
  width: 200px;
}

.container > button:hover {
  background-color: green;
}